import './../../App.css';
import './../../styles/css/inputs.css';

const InputRow = ({word, rowIndex, onSubmit, onChange, guessedWord}) => {

    const handleEnter = (event, index) => {
        // const key = event.key.toLowerCase();
        // if ((/[a-zA-Z]/).test(key) && key.length === 1) {
        const {value} = event.target;
        console.log({value})
        if (value.length > 0) {
            if (index < word.length - 1) {
                const form = event.target.form;
                // console.log({form})
                const index = [...form].indexOf(event.target);
                form.elements[index + 1].focus();
                event.preventDefault();
                onChange(event.target.value, index);
            } else {
                const form = event.target.form;
                form.elements[0].focus();
                onSubmit(event.target.value, index);
            }
        }
        // }
    };

    const handleInput = (event, index) => {
        const key = event.key.toLowerCase();

        if ((/[a-zA-Z]/).test(key) && key.length === 1) {

            const form = event.target.form;

            if (index < word.length - 1) {
                const index = [...form].indexOf(event.target);
                form.elements[index + 1].focus();
                event.preventDefault();
                onChange(key, index);
            } else {
                form.elements[0].focus();
                onSubmit(key, index);
            }

        } else if (key === 'backspace' || key === 'delete') {
            onChange('', index);
        }
    };

    return (
        <form>
            {word.split('').map((letter, index) => {
                // if (index < (word.length - 1)) {
                return (
                    <input key={`${letter}_${index}_${rowIndex}`}
                        // onKeyDown={(event) => {
                        //     handleEnter(event, index)
                        // }}
                           onKeyUp={(event) => {
                               handleInput(event, index)
                           }}
                           style={{marginTop:10}}
                           className="letter-input"
                           maxLength="1"
                           onChange={() => {
                           }}
                        // onChange={(event) => {
                        //     handleEnter(event, index)
                        // }}
                           value={guessedWord[index] ? guessedWord[index] : ''}
                        // placeholder={letter}
                    />
                )
                // } else {
                //     return (
                //         <input key={`${letter}_index`}
                //                onKeyDown={(value) => onSubmit('TESTE')}
                //                className="letter-input"
                //                maxLength="1"
                //                value={guessedWord[index]?guessedWord[index]:''}
                // placeholder={letter}
                // />
                // )
                // }
            })}
        </form>
    );
}

export default InputRow;
