import {
    SET_GUESSED_LETTER,
    SET_TARGET_WORD,
    SET_CORRECT_ANSWER,
    SET_SCORE,
    SET_LENGTH,
    CLEAR_GUESSED_WORD,
    CLEAR_PREVIOUS_GUESSED_WORDS,
    ADD_GUESSED_WORD
} from "../../constants/action-types";

const initialState = {
    targetWord: '',
    correctAnswer:'',
    targetWordDefinition: '',
    guessedWord: ['','','','','','',''],
    previousGuessedWords: [],
    length:4,
    score:0,
    progress: 1
}

const setGuessedWord = (guessedWord, value) => {
    let tempGuessedWord = guessedWord;
    guessedWord[value.index] = value.letter;
    return tempGuessedWord;
}

const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_GUESSED_LETTER:
            return {
                ...state,
                guessedWord: setGuessedWord(state.guessedWord, action.value)
            }
        case CLEAR_GUESSED_WORD:
            return {
                ...state,
                guessedWord: Array.from({length: state.targetWord.length}, (_, i) => '')
            }
        case CLEAR_PREVIOUS_GUESSED_WORDS:
            return {
                ...state,
                previousGuessedWords:[]
            }
        case SET_TARGET_WORD:
            return {
                ...state,
                targetWord: action.value.word.toUpperCase(),
                // targetWordDefinition: action.value.definition,
                guessedWord: Array.from({length: action.value.word.length}, (_, i) => '')
            }
        case SET_CORRECT_ANSWER:
            return {
                ...state,
                correctAnswer: action.value
            }
        case SET_SCORE:
            return {
                ...state,
                score: (state.score+action.value)
            }
        case SET_LENGTH:
            return {
                ...state,
                length: action.value
            }
        case ADD_GUESSED_WORD:
            console.log([
                ...state.previousGuessedWords,
                state.guessedWord.join('')
            ])
            return {
                ...state,
                previousGuessedWords: [
                    state.guessedWord.join(''),
                    ...state.previousGuessedWords,
                ],
                guessedWord: Array.from({length: state.targetWord.length}, (_, i) => '')
            }
        default:
            return state;
    }
}

export default Reducer;
