import {
    SET_CORRECT_ANSWER,
    SET_SCORE,
    SET_GAME_TIME,
    SET_SECONDS_ELAPSED,
    SET_TRANSITIONING
} from "../../constants/action-types";
// import {API} from "../../utils/requests";
// import {toast} from "react-toastify";

const actions = {
    setScore: (length) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            let score = length * 250;
            dispatch({type: SET_SCORE, value: score});
            dispatch({type: SET_CORRECT_ANSWER});
            resolve(true)
        })
    },
    setGameTime: (time) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_GAME_TIME, value: time});
            resolve(true)
        })
    },
    setSecondsElapsed: (time) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_SECONDS_ELAPSED, value: time});
            resolve(true)
        })
    },
    startColourTransition: (time) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_TRANSITIONING, value: true});
            resolve(true)
        })
    },
};

export default actions;
