import './../../App.css';
import './../../styles/css/inputs.css';
import useWord from "../../hooks/hooks.word";
import useGame from "../../hooks/hooks.game";
import Colours from "../../styles/colours";


const PreviousGuessRow = ({rowIndex, previousGuess}) => {


    const {
        targetWord,
    } = useWord(window);

    const {
        palette,
    } = useGame(window);

    const instancesOfLetter = (letter) => {
        let indexes = [], i = -1;
        while ((i = targetWord.indexOf(letter, i + 1)) >= 0) indexes.push(i);
        return indexes;
    }

    const getStyle = (letter, index) => {
        const letterIndexes = instancesOfLetter(letter.toUpperCase());

        if( !letterIndexes.length ){
            return {backgroundColor: `#FFFFFF20`, color: '#FFFFFF80'}
        }

        const bgColour = letterIndexes.indexOf(index) >= 0
            ? Colours.palettes[palette].correctPosition
            : Colours.palettes[palette].wrongPosition

        return {
            backgroundColor: bgColour,
            color: 'white',
            fontWeight: 900
        }

        // let letterIndexes = instancesOfLetter(letter.toUpperCase());
        // if (letterIndexes.length > 0) {
        //     if (letterIndexes.indexOf(index) >= 0) {
        //         return {
        //             backgroundColor: Colours.palettes[palette].correctPosition,
        //             color: 'white',
        //             fontWeight: 900
        //         }
        //     } else {
        //         return {
        //             backgroundColor: Colours.palettes[palette].wrongPosition,
        //             color: 'white',
        //             fontWeight: 900
        //         }
        //     }
        // } else {
        //     return {backgroundColor: `#FFFFFF20`, color: '#FFFFFF80'}
        // }
    }

    return (
        <form>
            {previousGuess.split('').map((letter, index) => {
                return (
                    <input key={`${letter}_${index}_${rowIndex}`}
                           disabled={true}
                           style={getStyle(letter, index)}
                           className={"letter-input mt-2"}
                           // className={"letter-input mb-2"}
                           maxLength="1"
                           value={letter}
                    />
                )
            })}
        </form>
    );
}

export default PreviousGuessRow;
