import Config from "../config";

const CALL = async (url, method, params) => {
    const headers = Config.defaultHeaders
    const body = JSON.stringify(params);
    const requestURL = Config.api + url;

    

    return new Promise(async (res, rej) => {

        // If no response after MAX_TIMEOUT seconds
        const timeout = setTimeout(() => {

            rej({message: "Request timed out."})
        }, 30000)

        fetch(requestURL, {
            method,
            body,
            headers
        }).then(resp => {
            
            clearTimeout(timeout);

            if (!resp.ok) {
                // checkStatusCodes(resp.status)
                throw resp
            }
            resp.json().then((responseData) => {
                //
                //
                // if (responseData.hasOwnProperty('error')) {

                    // rej(responseData);
                // } else {
                    
                    res(responseData);
                // }
            })
        }).catch(err => {

            if(err?.message === 'Failed to fetch'){
                // rej({})
                // checkStatusCodes(404)
            }else {
                clearTimeout(timeout);
                err.json().then(errorMessage => {
                    rej(errorMessage)
                })
            }
        })
    })
}

const API = {
    get: (url, params) => {
        return CALL(url, 'GET', params);
    },
    post: (url, params) => {
        return CALL(url, 'POST', params);
    },
    patch: (url, params) => {
        return CALL(url, 'PATCH', params);
    },
    delete: (url, params) => {
        return CALL(url, 'DELETE', params);
    },
}

export {API};
