import {
    CHANGE_PALETTE,
    SET_GAME_TIME,
    SET_MODE,
    SET_NEW_PALETTE,
    SET_SCORE,
    SET_SECONDS_ELAPSED,
    SET_TIMER,
    SET_TRANSITIONING
} from "../../constants/action-types";
import Colours from "../../styles/colours";

const initialState = {
    score: 0,
    timer: 0,
    gameTime: 60,
    secondsElapsed: 0,
    palette: 'a',
    nextPalette: 'b',
    mode: 'basic',
    transitioning: false
}

const pickRandomPalette = (palette) =>{
    const availablePalettes = Object.keys(Colours.palettes).filter(entry=>entry!==palette);
    return availablePalettes[Math.floor(Math.random() * availablePalettes.length)];
}


const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_SCORE:
            return {
                ...state,
                score: action.value
            }
        case SET_TIMER:
            return {
                ...state,
                timer: action.value
            }
        case SET_MODE:
            return {
                ...state,
                mode: action.value
            }
        case SET_GAME_TIME:
            return {
                ...state,
                mode: action.value
            }
        case SET_SECONDS_ELAPSED:
            return {
                ...state,
                secondsElapsed: (state.secondsElapsed+1)
            }
        case CHANGE_PALETTE:
            return {
                ...state,
                palette: pickRandomPalette(state.palette),
            }
        case SET_NEW_PALETTE:
            return {
                ...state,
            }
        case SET_TRANSITIONING:
            return {
                ...state,
            }
        default:
            return state;
    }
}

export default Reducer;
