    submitGuessedWord: (letter, index, guessedWord, targetWord) => async (dispatch) => {
        console.log("here")
        return new Promise(async (resolve, reject) => {
            let url = `word/check?word=${guessedWord.join('') + letter}`
            API.get(url).then((resp) => {
                if (resp.error) {
                    toast.error(resp.error)
                    dispatch({type: CLEAR_GUESSED_WORD, value: {letter, index}});
                    resolve(false)
                } else {
                    if((guessedWord.join('') + letter).toUpperCase()===targetWord.toUpperCase()){
                        dispatch({type: SET_CORRECT_ANSWER, value:targetWord});
                    }
                    dispatch({type: SET_GUESSED_LETTER, value: {letter, index}});
                    dispatch({type: ADD_GUESSED_WORD})
                    dispatch({type: CLEAR_GUESSED_WORD, value: {letter, index}});
                    resolve(true)
                }
            }, error => {

                // toast.error(`Something has gone awry...`)
                reject(error)
            })

        })
    },
