import {
    SET_MAX_ASCENDING_WORD_LENGTH,
    SET_MAX_WORD_LENGTH,
    SET_MIN_WORD_LENGTH,
    SET_SETTINGS_VISIBILITY
} from "../../constants/action-types";

const initialState = {
    minWordLength: 4,
    maxWordLength: 10,
    ascendingWordLength: true,
    settingsVisibility:false,
}

const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_MIN_WORD_LENGTH:
            return {
                ...state,
                minWordLength: action.value
            }
        case SET_MAX_WORD_LENGTH:
            return {
                ...state,
                maxWordLength: action.value
            }
        case SET_MAX_ASCENDING_WORD_LENGTH:
            return {
                ...state,
                ascendingWordLength: action.value
            }
        case SET_SETTINGS_VISIBILITY:
            return {
                ...state,
                settingsVisibility: action.value
            }
        default:
            return state;
    }
}

export default Reducer;
