import {
    SET_MAX_ASCENDING_WORD_LENGTH,
    SET_MAX_WORD_LENGTH, SET_MIN_WORD_LENGTH,SET_SETTINGS_VISIBILITY
} from "../../constants/action-types";
import {API} from "../../utils/requests";
import {toast} from "react-toastify";
// import {API} from "../../utils/requests";
// import {toast} from "react-toastify";

const actions = {
    setSettingsVisibility: (toggle) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_SETTINGS_VISIBILITY, value: toggle});
            resolve(true)
        })
    },
    setMaxWordLength: (length) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_MAX_WORD_LENGTH, value: length});
            resolve(true)
        })
    },
    setMinWordLength: (length) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_MIN_WORD_LENGTH, value: length});
            resolve(true)
        })
    },
    setAscendingWordLength: (toggle) => async (dispatch) => {
        return new Promise(async (resolve, reject) => {
            dispatch({type: SET_MAX_ASCENDING_WORD_LENGTH, value: toggle});
            resolve(true)
        })
    }
};

export default actions;
